.App {
  text-align: center;
}
.App-logo{
  margin: 10px auto;
  padding: 20px 0;
  width: 144px;
}
.App-logo img{
  object-fit: cover;
  width: 100%;
}
.App-title{
  color: var(--theme-body-txt);
  margin-top: 0.6rem;
}
.App-content {
  background-color: var(--theme-body-bg);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  padding: 16px;
  color: var(--theme-body-txt);
  
}
/************/
.App-main{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.App-wrapper{
  width: 100%;
  max-width: 90rem;
  margin: 0 auto;
  padding: 0 0.5rem;
}
.App-results{
  flex-shrink: 2;
  width: 100%;
  min-height: 200vh;
}
.App-category{
  margin-bottom: 25px;
  width: 100%;
}

button{
  background-color: #09f;
  border:0;
  color:#fff;
  padding: 8px 16px;
  font-size: 16px;
  border-radius: 10px 0 0 10px;
}
a{
  color: #09f;
  font-size: 32px;
}
form{
  display: flex;
  margin: 0 auto 30px;
  justify-content: center;
}
input[type=text]{
  border-radius: 0 10px 10px 0;
  font-size:16px;
  border: none;
  padding: 4px 16px;	
}
a{
  color:#09f;
  font-size: 32px;
  text-decoration: none;
}
ul{
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width:1240px) {
  .App-main{
    flex-direction: initial;
  }
  .App-category{
    position: sticky;
    top:var(--search-size-mx-h);
    width: 40%;
    max-width: 300px;
  }

}

.btn{
  border:1px solid transparent;
  padding: .5rem 1rem;
  background-color: var(--brand-color_3);
  color: var(--theme-body-txt);
  cursor: pointer;
  font-size: 1rem;
}
.btn[disabled]{
  opacity: .3;
  pointer-events: none;
}
.btn:hover{
  background-color: var(--brand-color_6);
}


